.format-panel {
  margin-left: 120px;
  margin-top: 150px;
  margin-bottom: 60px;
  width: 45%;
  background-color: rgba(149, 165, 166, 0.97);
  border-radius: 35px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  animation: floatIn 0.75s ease-out forwards;
  text-align: left;
}

.dpul-bg {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.1)),
    url("../images/landing/dp-ul.jpg") !important;
}

.sumlot-bg {
  background: url("../images/landing/sum-lot.jpg") !important;
}

.xysts-bg {
  background: url("../images/landing/xy-sts.jpg") !important;
}

.bwplf-bg {
  background: url("../images/landing/bw-plf.jpg") !important;
}

@media (max-width: 768px) {
  .format-panel {
    margin-left: auto;
    margin-right: auto;
    margin-top: 150px;
    width: 90vw !important;
    background-color: rgba(149, 165, 166, 0.97);
    border-radius: 35px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
    animation: floatIn 0.75s ease-out forwards;
    text-align: left;
  }
}

@keyframes floatIn {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html,
body {
  height: 100%;
}

.content {
  min-height: 100vh;
  height: auto;
  display: flex;
  flex-direction: column;
  text-align: justify;
  background: url("../images/landing/home.jpg");
  background-repeat: repeat;
}

.content.dimmer {
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.1)),
    url("../images/landing/home.jpg");
  background-size: auto;
  background-position: top left;
  background-repeat: repeat;
}
